<style>

html , body{
    width: 100%;
    background-color: rgb(154, 175, 95);
    height: 90%;
}

.login{
    width: 100%;
    height: 100%;
    background-color: #1c2438;
    position: relative;
}

.login .from-wrap{
    position: fixed;
    left: 50%;
    margin-left: -200px;
    top: 50%;
    margin-top: -150px;
    width: 400px;
    height: 240px;
    border-radius: 10px;
    background-color: #fff;
    padding: 20px 30px;
}

.login h2{
    text-align: center;
    margin-bottom: 10px;
}

.login i-form-item{
    margin-bottom: 15px;
}

.login .form-footer{
    text-align: right;
}

.ivu-form-item-required .ivu-form-item-label:before{
    display: none;
}

</style>

<template>
    <div class="login">
        <div class="from-wrap">
            <h2>登录</h2>
            <i-form ref="loginData" :model="loginData" :rules="ruleValidate" :label-width="80">
                <i-form-item label="用户名" prop="acct">
                    <i-input type="text" v-model="loginData.acct" placeholder="请输入账号" />
                </i-form-item>
                <i-form-item label="密码" prop="pass">
                    <i-input type="password" v-model="loginData.pass" placeholder="请输入密码" />
                </i-form-item>
                <div class="form-footer">
                    <i-button type="primary" @click="method_login">登录</i-button>
                    <i-button @click="method_regist" style="margin-left: 8px">注册</i-button>
                </div>
            </i-form>
        </div>
        
    </div>
</template>

<script>
import axios from "axios";
import { Message } from 'iview' 
// var src = 'http://192.168.1.134:9898/services/webservice.asmx/api/';
// var src = "/api/"
export default{
    name: 'LoginPage',
    data() {
        return {
            
            loginData: {
                acct:'',
                pass:''
            },
            ruleValidate:{
                acct:[
                    { required: true, message:'用户名不能为空', trigger:'blir'},
                    { min: this.$store.state.usernamelength.min, max: this.$store.state.usernamelength.max,message:'用户名长度3-16个字符',trigger:'blur'}
                ],
                pass: [
                    {required: true, message: '密码不能为空',trigger:'blur'},
                    { type:'string',min: this.$store.state.usernamelength.min, max: this.$store.state.usernamelength.max,message:'密码长度3-16个字符',trugger:'blur'}
                ]
            }
        }
    },
    computed:{
        
            
    },
    methods:{
        method_login: function(){
            
            window.console.log("login")

            var username = this.loginData.acct;
            var password = this.loginData.pass;

            if( username.length < 6 || username.length > 16)
            {
                Message.error('用户名长度6-16个字符')
                return ; 
            }   
            if( password.length < 6 || password.length > 16)
            {
                Message.error('密码长度6-16个字符')
                return ;
            }
                

            var params = new URLSearchParams();
          
          params.append('username', username);
            params.append('password', password);

            axios( {
                headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'},
                method: 'post',
                url: '/api/UserLogin',
                data: params,
                dataType: 'json'

            })
            .then( (response) =>{
                window.console.log(response.data)
                window.console.log( this.ruleValidate.acct)

                if( response.data == 'success')
                {
                    Message.success("登录成功");
                    this.$router.push('/mainpage')
                    return ;
                }
                if( response.data == 'nouser')
                {
                    Message.err('用户名不存在')
                    return ;
                }
                if( response.data == 'passworderror')
                {
                    Message.err( '密码错误')
                    return ;
                }
            })
            .catch( (error) => {
                window.console.log(error)
            })
    
        },
        method_regist: function(){
            // this.$refs[name].resetFields();
            this.$router.push('/registpage');
        }
    }
}
</script>